import { Fragment, h, JSX } from '@stencil/core';
import { MyPorsche } from '../../entities/content';
import { IsLoggedIn } from '../../utility/helper';
import { navStateStore } from '../../state/nav-state-store';

export default function Login({
  content,
  showSkeleton
}: {
  content: MyPorsche;
  showSkeleton: boolean;
}): JSX.Element | null {
  const { login, more, signup, signupText } = content;
  const { loggedInState } = navStateStore.state;
  const isLoggedIn = IsLoggedIn(loggedInState);

  if (isLoggedIn) {
    return null;
  }
  return (
    <div class="myporsche-login">
      {showSkeleton ? (
        <Fragment>
          <div class="skeleton-login-container">
            <div class="skeleton-login" />
            <div class="skeleton-more" />
          </div>

          <div class="skeleton-signup-container">
            <div class="skeleton-signup">{content.signupText}</div>
          </div>
        </Fragment>
      ) : null}
      <phn-myporsche-login
        login={login}
        more={more}
        signup={signup}
        signupText={signupText}
        style={{ display: showSkeleton ? 'none' : '' }}
      />
    </div>
  );
}
